@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Jura:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app{margin:0;padding:0}

.topbar{display:flex;position:fixed;top:0;left:0;height:32px;width:100vw;z-index:5;background-color:#2F2F2F;justify-content:space-between}.topbar__contact{display:flex;color:white;margin-left:50px;font-family:'Roboto', sans-serif;font-size:12px}.topbar__contact span{display:flex;line-height:32px;margin:0 10px;align-items:center}.topbar__contact span img{margin-right:5px}.topbar__social{margin-right:100px;display:flex;align-items:center}.topbar__social img{margin:0 10px}.topbar__social img:hover{cursor:pointer}@media (max-width: 1000px){.topbar__contact{margin-left:5px;font-size:10px}.topbar__contact span{margin:0 5px}.topbar__social{margin-right:10px}}

.menu{width:100vw;height:85px;background-color:rgba(47,47,47,0.6);display:flex;justify-content:flex-end;z-index:5;position:fixed;top:32px;left:0}.menu__button{font-family:'Jura', sans-serif;font-weight:300;font-size:24px;text-align:center;line-height:85px;color:#FFFFFF;width:15vw;height:100%;transition:.3s}.menu__button:hover{cursor:pointer;background-color:rgba(47,47,47,0.8)}.menu__button:last-child{margin-right:50px}a{color:white;text-decoration:none}@media (max-width: 1000px){.menu{height:50px;justify-content:space-evenly}.menu__button{width:25vw;display:flex;align-items:center}.menu__button:last-child{margin-right:0}.menu__button img{height:30px;margin-left:calc(12.5vw - 15px)}}

.main{width:100vw;height:100vh;background:url(/carshine/static/media/hero.4db87eac.png);background-size:cover;background-position:center;position:relative;margin-top:32px;z-index:3}.introduce{width:100vw;min-height:100vh;background-color:#2F2F2F}.introduce__title{box-sizing:border-box;width:80vw;margin-left:10vw;font-family:'Jura', sans-serif;font-weight:300;font-size:28px;color:#FFFFFF;text-align:center;padding:50px}.introduce__advertbox{display:flex;width:80vw;margin-left:10vw;justify-content:space-between}.introduce__advertbox__advert{display:flex;flex-direction:column;font-family:'Montserrat', sans-serif;font-size:14px;text-align:center;color:#FFFFFF;align-items:center}.introduce__advertbox__advert img{box-sizing:border-box;padding:10px;width:80%}.introduce__advertbox__advert__text{width:70%;margin-top:10px;padding-bottom:30px}.offer__backgr{width:100vw;min-height:50vh;background:url(/carshine/static/media/section2.c9a7c24a.png);background-size:cover;background-position-y:center;background-attachment:fixed;color:#FFFFFF}.offer__backgr__title{font-family:'Jura', sans-serif;font-weight:300;font-size:28px;text-align:center;padding:70px}.offer__backgr__description{font-family:'Montserrat', sans-serif;font-size:18px;text-align:center;font-weigth:400;width:40vw;margin-left:30vw;padding-bottom:50px}.offer__textbox{min-height:50vh;background-color:#2F2F2F}.offer__textbox__title{font-family:'Jura', sans-serif;font-weight:300;font-size:28px;text-align:center;padding:50px;color:#FFFFFF}.offer__textbox__container{width:100vw;display:flex;justify-content:space-evenly;padding-bottom:100px}.offer__textbox__container__box{width:25vw;display:flex;flex-direction:column;align-items:center}.offer__textbox__container__box__rectangle{width:50px;height:50px;background-color:#861616;box-shadow:0px 4px 4px rgba(0,0,0,0.1);transform:rotate(-45deg);margin-bottom:30px}.offer__textbox__container__box__text{font-family:'Montserrat', sans-serif;font-size:14px;text-align:center;font-weigth:400;color:#FFFFFF;width:60%}.bottomtext{width:100vw;height:150px;line-height:150px;text-align:center;font-family:'Jura', sans-serif;font-weight:300;font-size:48px;color:#861616;background-color:#DADADA}@media (max-width: 1000px){.introduce__advertbox{flex-direction:column}.introduce__advertbox__advert{padding-bottom:20px}.offer__textbox__container{flex-direction:column}.offer__textbox__container__box{margin-left:10vw;width:80vw}.offer__textbox__container__box__text{padding-bottom:50px}.bottomtext{min-height:150px;font-size:24px}}

.logo{width:100vw;height:55vh;background:linear-gradient(rgba(0,0,0,0.6), rgba(0,0,0,0.6)),url(/carshine/static/media/hero.4db87eac.png);background-size:cover;background-position:center;position:relative;margin-top:32px;z-index:3}.logo__txt{color:#ffffff;font-family:'Montserrat', sans-serif;font-size:48px;margin-left:5vw;padding-top:40vh;line-height:10vh}.gallery{width:100vw;min-height:50vh;background-color:#C4C4C4}.gallery__title{font-family:'Jura', sans-serif;font-weight:300;font-size:28px;text-align:center;line-height:85px;color:#000000}.gallery__container{width:100vw;display:flex;flex-direction:column}.gallery__container__div{width:30vw;margin-left:35vw;padding-bottom:30px}ul.control-dots{background-color:rgba(0,0,0,0)}@media (max-width: 1000px){.gallery__container__div{width:70vw;margin-left:15vw}}

.logo{width:100vw;height:55vh;background:linear-gradient(rgba(0,0,0,0.6), rgba(0,0,0,0.6)),url(/carshine/static/media/hero.4db87eac.png);background-size:cover;background-position:center;position:relative;margin-top:32px;z-index:3}.logo__txt{color:#ffffff;font-family:'Montserrat', sans-serif;font-size:48px;margin-left:5vw;padding-top:40vh;line-height:10vh}.pricing{width:100vw;min-height:50vh;padding-bottom:85px;background-color:#E5E5E5}.pricing__title{font-family:'Jura', sans-serif;font-weight:300;font-size:28px;text-align:center;line-height:85px;color:#000000}.pricing__container{box-sizing:border-box;background-color:#C4C4C4;width:80vw;margin-left:10vw;min-height:45vh;padding:20px;display:flex;justify-content:space-evenly}.pricing__container__pricebox{display:flex;flex-direction:column;align-items:center;text-align:center}.pricing__container__pricebox__price{font-family:'Jura', sans-serif;font-weight:700;font-size:18px;color:#861616;padding:10px}.pricing__container__pricebox__description{font-family:'Montserrat', sans-serif;font-size:14px;width:80%}@media (max-width: 1000px){.pricing__container{flex-direction:column}.pricing__container__pricebox{padding-bottom:20px}}

.logo{width:100vw;height:55vh;background:linear-gradient(rgba(0,0,0,0.6), rgba(0,0,0,0.6)),url(/carshine/static/media/hero.4db87eac.png);background-size:cover;background-position:center;position:relative;margin-top:32px;z-index:3}.logo__txt{color:#ffffff;font-family:'Montserrat', sans-serif;font-size:48px;margin-left:5vw;padding-top:40vh;line-height:10vh}.contact__map{width:100vw;min-height:50vh;padding-bottom:50px}.contact__info{background-color:#E5E5E5}.contact__info__title{font-family:'Jura', sans-serif;font-weight:700;font-size:24px;text-align:center;line-height:85px;color:#000000}.contact__info__address{font-family:'Montserrat', sans-serif;font-size:14px;text-align:center}.contact__info__social{text-align:center;padding:20px}.contact__info__social img{width:40px;padding:10px;transition:.3s}.contact__info__social img:hover{cursor:pointer;box-shadow:0 0 8px #888888}

.map-h2 {
    text-transform: uppercase;
    font-size: 1rem;
    padding: 20px;
    padding-left: 10px;
    text-align: center;
  }
  
  .google-map {
    width: 100%;
    height: 60vh;
  }
  
  .pin {
    display: flex;
    align-items: center;
    width: 180px;
    color: var(--main-blue);
  }
  
  .pin-icon {
    font-size: 4rem;
  }
  
  .pin-text {
    font-size: 1.3em;
  }
  
  @media screen and (min-width: 1001px) {
    .google-map {
      height: 80vh;
    }
  
    .map-h2 {
      font-size: 1.3rem;
      font-weight: 400;
    }
  
    .pin {
      width: 15vw;
    }
  
    .pin-icon {
      font-size: 10vw;
    }
  }
.footer{width:100vw;height:100px;background-color:#2F2F2F;display:flex;justify-content:space-between}.footer__contact{display:flex;flex-direction:column;color:white;margin-top:18px;margin-left:50px;font-family:'Roboto', sans-serif;font-size:10px}.footer__contact span{display:flex;line-height:32px;margin:0 10px;align-items:center}.footer__contact span img{margin-right:5px}

