.logo {
    width: 100vw;
    height: 55vh;
    background: linear-gradient( rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6) ), url('./img/hero.png');
    background-size: cover;
    background-position: center;
    position: relative;
    margin-top: 32px;
    z-index: 3;

    &__txt {
        color: #ffffff;
        font-family: 'Montserrat', sans-serif;
        font-size: 48px;
        margin-left: 5vw;
        padding-top: 40vh;
        line-height: 10vh; } }

.contact {
    &__map {
        width: 100vw;
        min-height: 50vh;
        padding-bottom: 50px; }

    &__info {
        background-color: #E5E5E5;

        &__title {
            font-family: 'Jura', sans-serif;
            font-weight: 700;
            font-size: 24px;
            text-align: center;
            line-height: 85px;
            color: #000000; }

        &__address {
            font-family: 'Montserrat', sans-serif;
            font-size: 14px;
            text-align: center; }

        &__social {
            text-align: center;
            padding: 20px;

            img {
                width: 40px;
                padding: 10px;
                transition: .3s;

                &:hover {
                    cursor: pointer;
                    box-shadow: 0 0 8px #888888; } } } } }
